/* GOOGLE  */

.GOOGLE button {
    background-color: rgb(255, 255, 255);
    display: inline-flex;
  
    align-items: center;
    color: rgba(0, 0, 0, 0.54);
    box-shadow: 0px !important;
    padding: 0px;
    border: 0px solid transparent !important;
    font-family: Roboto, sans-serif;
  }
  
  button.GOOGLE div {
    background-color: #f94033 !important;
    display: none;
  }
  
  .GOOGLE {
    background-color: #f94033 !important;
    margin-bottom: 10px;
    height: 4.5vh;
    text-align: center !important;
    border: 0 !important;
  }
  .GOOGLE span {
    color: #eaebed;
    /* font-family: Helvetica,sans-serif !important; */
    font-size: 14px;
    font-weight: 500;
    margin: auto;
  }
  
  /* Facebook  */
  
  .kep-login-facebook.metro {
    border-radius: 0;
    padding: 0px;
  }
  
  .kep-login-facebook {
    font-family: Helvetica, sans-serif;
    font-weight: 700;
    -webkit-font-smoothing: antialiased;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 0.9vw !important;
    text-decoration: none;
    text-transform: uppercase;
    transition: background-color 0.3s, border-color 0.3s;
    background-color: #0073b1 !important;
    border-radius: 25px !important;
    border: 0px !important;
    padding: 10px 10px 10px 10px;
    width: 20vw;
    margin-bottom: 10px;
    height: 4.5vh;

  }
  
  /* Github  */
  
  .GithubLogin {
    background-color: #0148a4 !important;
    width: 20vw;
    margin-bottom: 10px;
    height: 4.5vh;
    margin-bottom: 10px;
    color: #eaebed;
  
    /* font-family:Verdana !important; */
    font-weight: 600 !important;
    border-color: #0148a4 !important;
    border-radius: 25px !important;
    border: 0px !important;
    font-size: 0.9vw !important;
  }
  
  /* microsoft  */
  
  .btn-microsoft-login {
    background-color: #0073b1 !important;
    width: 20vw;
    margin-bottom: 10px;
    height: 4.5vh;
    color: #eaebed;
    /* font-family: Helvetica,sans-serif !important; */
    font-weight: 600 !important;
    font-size: 0.9vw !important;
  }
  
  /* Twitter  */
  
  .TwitterLogin {
    border-radius: 25px !important;
    background-color: #0073b1 !important;
    width: 20vw;
    height: 4.5vh;
   
    margin-bottom:  10px;
  }   
  
  .TwitterLogin rect{
      background-color:#0073b1 !important;
      fill:#0073b1 !important;
      border-radius: 25px !important;
      stroke:#0073b1 !important;
     
      display: none;
    }
  
  
    .TwitterLogin path{
     
      fill:#EAEBED !important;
      border-radius: 25px !important;
    
  
      display: none;
    }
    div#TwitterLogin_main:before {
      content: "LOGIN WITH TWITTER";
      position: absolute;
      /* margin: 10px; */
      /* text-align: center !important; */
      left: 30%;
      /* top: 5%; */
      margin-top: 8% !important;
      font-size: 1vw !important;
      font-weight: 600 !important;
      color: #eaebed;
  }
  /* Linkedin  */
  
  .Linkedin {
    background-color: #f94033 !important;
    width: 20vw;
    margin-bottom: 10px;
    height: 4.5vh;
    text-align: center !important;
    border-radius: 25px !important;
    height: 43px;
    margin-bottom: 30px;
  }
  
  .btn-linkedin::before {
    content: "LOGIN WITH LINKEDIN";
    font-size: 1vw !important;
    position: absolute;
      /* margin: 10px; */
      /* text-align: center !important; */
      left: 30%;
      /* top: 5%; */
      margin-top: -2% !important;
      font-size: 1vw !important;
      font-weight: 600 !important;
      color: #eaebed;
  }
  .btn-linkedin img {
    display: none;
  }
  .btn-linkedin {
    color: #fff;
    background-color: #0073b1 !important;
    border-color: #0073b1 !important;
    height: 43px;
    padding: 0px;
    width: 20vw;
    border-radius: 25px;
    content: "";
    margin-bottom: 10px;
  }
  
  .btn-linkedin {
    color: #fff;
    background-color: #0073b1 !important;
    border-color: #0073b1 !important;
   
    width: 20vw;
    margin-bottom: 10px;
    height: 4.5vh;
    
  }
  
  /* Instagram */
  
  .InstagramLogin {
    display: inline-block;
    background: linear-gradient(
      rgb(101, 89, 202),
      rgb(188, 49, 143) 30%,
      rgb(227, 63, 95) 50%,
      rgb(247, 118, 56) 70%,
      rgb(254, 198, 109) 100%
    );
    color: rgb(255, 255, 255);
    width: 20vw !important;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 25px;
  ;
    font-size: 0.9vw !important;
    font-weight: bold;
    font-family: proxima-nova, "Helvetica Neue", Arial, Helvetica, sans-serif;
  }
  
  .extra-logins body {
    background-color: #fbfbfb !important;
  }
  
  .extra-logins img {
    width: 215px;
    max-width: unset !important;
    margin: 0 auto;
    /* margin-top: em;
        height: 43px;
        width: 20vw;
        max-width: 20vw !important; */
  }
  
  .extra-logins button, .extra-logins svg {
    border: none !important;
    outline: none !important;
    margin: 0 auto;
  }

  .extra-logins > div > div {
    width: 215px;
    margin: 0 auto;
  }

  .extra-logins span {
    padding: 0px !important;
  }
  
  .paypal-button:not(.paypal-button-card) {
    height: 43px !important;
    min-height: 35px;
    max-height: 55px;
    min-width: 250px !important;
    margin-top: 10px;
  }
  .paypal-button {
    background-color: #2c2e2f !important;
    width: 20vw;
    margin-bottom: 10px;
    height: 43px !important;
    text-align: center !important;
    border-radius: 25px !important;
    border: 0 !important;
  }
  
  .extra-logins {
    border-top: 2px solid var(--light-blue);
    margin-top: 25px;
  }

  .login-options {
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    height: 125px;
    justify-content: space-between;
  }
  